.projects {
  background: $bgSecondary;
  background: linear-gradient(
    345deg,
    rgba(50, 54, 64, 1) 52%,
    rgba(60, 65, 78, 1) 52%
  );
  padding: 5% 0;
}
.projectsTitle {
  h2 {
    color: $accent2;
    letter-spacing: 2px;
    text-align: center;
    padding-bottom: 4%;
    font-size: 1.8rem;
  }
}
.project1,
.project2,
.project3,
.project4,
.active {
  display: flex;
  justify-content: center;
}
.hidden {
  display: none;
}
.projectLaptop {
  width: 45%;
  .projectPhoto {
    width: 319px;
    position: absolute;
    top: 26px;
    right: 66px;
  }
  .laptopImage {
    width: 450px;
    position: relative;
  }
}
.projectInfo {
  width: 45%;
  color: white;
  .skillUsed {
    color: $accent3;
    padding-top: 34px;
    padding-bottom: 30px;
  }
  a {
    padding: 15px 15px;
    margin-right: 10px;
    background: $bgThird;
    color: white;
    letter-spacing: 2px;
    border: 2px solid $accent1;
  }
  a:hover {
    color: $accent2;
  }
  h2 {
    color: $accent1;
    padding-bottom: 5px;
  }
}

.left,
.right {
  color: $accent1;
  background: none;
  border: none;
  font-size: 51px;
  padding-right: 5px;
  padding-left: 5px;
}
.left:hover,
.right:hover {
  color: $accent4;
}
.desktopArrow {
  display: block;
}
.mobileArrows {
  display: none;
}

//SMALLER TO LARGER SCREENS
@media (max-width: 1250px) {
  .projectLaptop {
    .projectPhoto {
      width: 271px;
      top: 21px;
      right: 55px;
    }
    .laptopImage {
      width: 380px;
    }
  }
  .projectInfo {
    margin-left: 30px;
  }
  .left,
  .right {
    font-size: 40px;
  }
}
//DESKTOP STYLES
@media (max-width: 940px) {
  .projectExample {
    margin-bottom: 20px;
  }
  .project1,
  .project2,
  .project3,
  .project4 {
    flex-direction: column;
    align-items: center;
  }
  .projectLaptop {
    width: 80%;
    padding-bottom: 10px;
    display: flex;
    justify-content: center;
  }
  .projectInfo {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .desktopArrow {
    display: none;
  }
  .mobileArrows {
    display: block;
    margin-bottom: 27px;
  }
}
@media (max-width: 621px) {
  .projectLaptop {
    width: 348px;
    .projectPhoto {
      width: 251px;
      top: 19px;
      right: 48px;
    }
  }
}
@media (max-width: 400px) {
  .projectLaptop {
    width: 300px;
    .projectPhoto {
      width: 217px;
      top: 16px;
      right: 42px;
    }
  }
}
@media (max-width: 333px) {
  .links {
    display: flex;
    flex-direction: column;
    .liveLink {
      margin-bottom: 17px;
      text-align: center;
    }
  }
}
