.aboutMe {
  background: linear-gradient(
    30deg,
    rgba(38, 41, 54, 1) 52%,
    rgba(46, 49, 65, 1) 52%
  );
  padding: 5% 0;
  min-height: 70vh;
}
.aboutMeTitle {
  color: $accent3;
  letter-spacing: 1.7px;
  font-size: 1.2rem;
}
.bio {
  color: white;
}
.info {
  width: 65%;
  padding-left: 51px;
  p {
    padding: 10px 0px;
    line-height: 1.5;
    letter-spacing: 2px;
    font-size: 0.9rem;
  }
}

.about {
  display: flex;
  .headshot {
    width: 35%;
    padding-top: 9px;
  }
}

//SMALLER TO LARGER SCREENS
@media (max-width: 1080px) {
  .about {
    flex-direction: column;
    align-items: center;
    .headshot {
      width: 30%;
    }
    .info {
      width: 100%;
      padding-left: 0;
    }
  }
}

//DESKTOP STYLES
@media (max-width: 940px) {
  .showMore {
    display: block;
  }

  .about {
    .headshot {
      width: 46%;
    }
  }
  .hideMobile {
    display: none;
  }
}
//IPAD STYLES
@media (max-width: 768px) {
  .aboutMeTitle {
    letter-spacing: 1.5px;
    font-size: 1rem;
  }
  .info {
    p {
      line-height: 1.3;
      letter-spacing: 1.8px;
    }
  }
  .about {
    .headshot {
      width: 50%;
    }
  }
}
@media (max-width: 560px) {
  .info {
    p {
      font-size: 0.8rem;
    }
  }
  .about {
    .headshot {
      width: 65%;
    }
  }
}
@media (max-width: 480px) {
  .aboutMeTitle {
    font-size: 0.9rem;
  }
  .about {
    .headshot {
      width: 75%;
    }
  }
}
@media (max-width: 400px) {
  .info {
    p {
      font-size: 0.8rem;
      letter-spacing: 2.6px;
    }
  }
}
@media (max-width: 400px) {
  .about {
    .headshot {
      width: 100%; //screwing img size up
    }
  }
}
